<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>评论管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <div class="tool_group">
        <div class="btn_box">
          <router-link :to="{ path: '/admin/adAdd' }"><el-button class="inquire1" type="success" icon="el-icon-plus" size="mini">添加广告</el-button></router-link>
          <!-- 后端没接口<el-button type="danger" icon="el-icon-delete" size="mini">批量删除</el-button> -->
        </div>
        <el-form ref="ruleForm" :inline="true" :model="search" label-width="80px">
          <el-form-item label="地区" prop="area">
            <areaSelectCmp2 :areaParent="search.area" size="mini" @childFn2="onChildFn2"></areaSelectCmp2>
          </el-form-item>
          <el-form-item label="广告名称" prop="name">
            <el-input v-model="search.name" class="input_name" size="mini" />
          </el-form-item>
          <el-form-item>
            <el-button class="inquire" type="success" style="width: 80px" size="mini" @click="onSearch">查询</el-button>
            <el-button style="width: 80px" size="mini" @click="resetForm">重置</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="main_group">

        <div class="table_box">
          <el-table :data="AdList" style="width: 100%" height="500">
            <el-table-column type="selection" width="55" align="center" />
            <el-table-column prop="sn" label="排序" align="center" />
            <el-table-column label="图片" width="250" align="center">
              <template slot-scope="scope">
                <el-image class="cover" :src="scope.row.imageUrl" fit="cover" />
              </template>
            </el-table-column>
            <el-table-column prop="area" label="地区" align="center" />
            <el-table-column prop="advertisingUrl" label="链接" width="250" align="center" />
            <el-table-column prop="createTime" label="加入时间" align="center" show-overflow-tooltip />
            <el-table-column label="操作" width="150" align="center">
              <template slot-scope="scope">
                <div class="switch_group">
                  <div class="switch_btn">
                    <el-switch v-model="scope.row.enableStatus" active-color="#13ce66" inactive-color="#ff4949" :width="50" style="pointer-events: auto" @change="onSwitch(scope)" />
                    <!-- 是否启用状态 0-未启用 1-已启用 -->
                    <span v-if="scope.row.enableStatus" class="switch_text_true">启用</span>
                    <span v-else class="switch_text_false">禁用</span>
                  </div>
                  <router-link class="edit_btn" :to="{ path: '/admin/adAdd', query:{ id: scope.row.id }}">编辑</router-link>
                  <el-button type="text" @click="deleteAd(scope, AdList)"><span class="delete_btn">删除</span></el-button>
                </div>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination background layout="prev, pager, next" :total="AdListTotal" @current-change="handleCurrentChange" />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import areaSelectCmp2 from '@/components/area_select_cmp/index2.0.vue'

export default {
  name: 'AdList',

  data() {
    return {
      pageNo: 1, // 广告列表分页默认为1第一页
      AdList: [], // 广告列表
      AdListTotal: 0, // 广告分页总条数
      search: {
        area: '',
        name: '',
      }, // 搜索的数据
    }
  },

  components: {
    areaSelectCmp2,
  },

  methods: {
    // 删除广告
    deleteAd(scope, rows) {
      this._deleteAdItem(scope.row.id)
        .then((res) => {
          if (res) {
            rows.splice(scope.$index, 1)
          }
        })
    },
    // 分页
    handleCurrentChange(val) {
      this.pageNo = val
      this._getAdList()
    },
    // 启用/禁用
    onSwitch(scope) {
      // 是否启用状态 0-未启用 1-已启用
      const enableStatus = scope.row.enableStatus ? 0 : 1
      this.apiEnableOrdisable(scope.row.id, enableStatus)
    },
    // 选择导航栏
    handleSelect (e) {
      this._getAdList()
    },
    // 查询
    onSearch () {
      this._getAdList()
    },
    // 选择地区子组件向父组件传值
    onChildFn2(e) {
      this.search.area = e
    },
    resetForm () {
      this.search.area = []
      this.$refs.ruleForm.resetFields()
    },

    // 接口api--------------------------------------
    // 获取广告列表
    _getAdList() {
      const DATA = {
        pageNo: this.pageNo,
        ...this.search // 模糊查询
      }
      this.$http.post('/woho/advertising/manage/query', DATA)
        .then((res) => {
          console.log(res)
          const AdList = res.data.data.list
          AdList.map((item) => {
            // 是否启用状态 0-未启用 1-已启用
            if (item.enableStatus === 0 || item.enableStatus === '') {
              item.enableStatus = true
            } else {
              item.enableStatus = false
            }
          })
          this.AdList = AdList
          this.AdListTotal = res.data.data.total
        })
    },
    // 删除一条广告
    _deleteAdItem(id) {
      return this.$http.delete(`/woho/advertising/manage/delete/${id}`)
        .then((res) => {
          return res.data.success
        })
    },
    // 启用/禁用
    apiEnableOrdisable(id, enableStatus) {
      this.$http.post(`/woho/advertising/manage/enableOrdisable/${id}/${enableStatus}`)
    },
  },

  created () {
    this._getAdList()
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .box-card {
    min-width: 1100px;
    margin-top:15px;
    .tool_group{
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-top: 30px;
      .input_name{
        width: 200px;
        margin: auto 5px;
      }
    }
    .main_group{
      width: 100%;
      .table_box{
        margin: 48px 0px 0px 9px;
        width:100%;
        .cover{
          width: 215px;
          height: 99px;
          border-radius: 5px;
        }
        .switch_group{
          display: flex;
          align-items: center;
          .switch_btn{
            margin-right: 10px;
            position: relative;
            color: #fff;
            font-size: 8px;
            .switch_text_true{
              position: absolute;
              top: 1px;
              left: 5px;
              pointer-events: none;
            }
            .switch_text_false{
              position: absolute;
              top: 1px;
              left: 20px;
              pointer-events: none;
            }
          }
          .edit_btn{
            color: $themColor;
            margin-right: 10px;
          }
          .delete_btn{
            color: #dd3737;
          }
        }
      }
    }
  }
</style>
